.estimate
  padding-top: 185px
  padding-bottom: 195px
  @media screen and ( max-width: 992px )
    padding-top: 150px
    padding-bottom: 160px
  @media screen and ( max-width: 576px )
    padding-top: 70px
    padding-bottom: 80px

  &__container
    max-width: 1075px

  &__wrap
    display: flex
    align-items: center
    justify-content: center
    @media screen and ( max-width: 768px )
      flex-direction: column

  &__content
    display: flex
    flex-direction: column
    align-items: flex-start
    max-width: 320px
    @media screen and ( max-width: 768px )
      max-width: 100%
      margin-bottom: 20px

  &__title
    margin-bottom: 10px

  &__text
    font-weight: 400
    text-shadow: -6px 62px 25px rgba(0, 0, 0, 0.01), -3px 35px 21px rgba(0, 0, 0, 0.05), -1px 16px 16px rgba(0, 0, 0, 0.09), 0px 4px 9px rgba(0, 0, 0, 0.1), 0px 0px 0px rgba(0, 0, 0, 0.1)

  &__inner-img
    max-width: 753px
    line-height: 0

  &__img
    width: 100%
    height: auto
