body {
  margin: 0;
  font-family: "Golos-Text_Regular", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: #1f252d;
}
body.lock {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1300px;
  padding: 0 15px;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: inherit;
}

.link-reset {
  text-decoration: none;
  color: inherit;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.title {
  margin: 0;
  font-family: "Golos-Text_DemiBold", sans-serif;
  font-weight: 600;
  font-size: 32px;
  line-height: 98.5%;
  color: #1f252d;
}

.text {
  margin: 0;
  line-height: 113%;
}

.link {
  padding: 23px 48px 23px 78px;
  font-family: "Golos-Text_Medium", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 113%;
  background-color: #25252c;
  border: 2px solid #25252c;
  color: #ffffff;
  box-shadow: -6px 62px 25px rgba(0, 0, 0, 0.01), -3px 35px 21px rgba(0, 0, 0, 0.05), -1px 16px 16px rgba(0, 0, 0, 0.09), 0px 4px 9px rgba(0, 0, 0, 0.1), 0px 0px 0px rgba(0, 0, 0, 0.1);
  border-radius: 55px;
  position: relative;
  transition: all 0.4s;
}
.link::after {
  position: absolute;
  content: "";
  top: 25px;
  left: 55px;
  clip-path: polygon(0 0, 0 100%, 100% 50%);
  background-color: #ffffff;
  width: 7px;
  height: 12px;
}
.link:hover {
  background-color: inherit;
}

.tab-title {
  margin: 0;
  font-family: "Oranienbaum-Regular", sans-serif;
  font-weight: 400;
  font-size: 56px;
  line-height: 113%;
  color: #ffffff;
  opacity: 0.8;
  text-shadow: -6px 62px 25px rgba(0, 0, 0, 0.01), -3px 35px 21px rgba(0, 0, 0, 0.05), -1px 16px 16px rgba(0, 0, 0, 0.09), 0px 4px 9px rgba(0, 0, 0, 0.1), 0px 0px 0px rgba(0, 0, 0, 0.1);
}
@media screen and (max-width: 1220px) {
  .tab-title {
    text-align: center;
  }
}
@media screen and (max-width: 768px) {
  .tab-title {
    font-size: 36px;
  }
}

.tab-text {
  margin: 0;
  font-family: "Inter-LightBETA", sans-serif;
  font-weight: 300;
  line-height: 114%;
  letter-spacing: -0.02em;
  color: #ffffff;
  text-shadow: -6px 62px 25px rgba(0, 0, 0, 0.01), -3px 35px 21px rgba(0, 0, 0, 0.05), -1px 16px 16px rgba(0, 0, 0, 0.09), 0px 4px 9px rgba(0, 0, 0, 0.1), 0px 0px 0px rgba(0, 0, 0, 0.1);
  opacity: 0.6;
}
@media screen and (max-width: 1220px) {
  .tab-text {
    text-align: center;
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
body.lock {
  overflow: hidden;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 25px 0;
}
.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  position: relative;
  width: 79px;
  height: 26px;
  z-index: 5;
}
.logo__img {
  width: 100%;
  height: 100%;
}

.burger {
  display: none;
}

.menu__item {
  position: relative;
  color: #ffffff;
}
.menu__item::after {
  position: absolute;
  content: "";
  width: 0;
  border-top: 2px dashed white;
  top: 100%;
  right: 0;
  transition: all 0.4s;
}
.menu__item:hover::after {
  width: 100%;
  left: 0;
}

/*-------------------------*/
body.touch .menu__link {
  flex: 1 1 auto;
  transition: transform 0.4s ease 0s;
}

/*-------------------------*/
@media screen and (min-width: 940px) {
  .menu {
    display: flex;
    align-items: center;
  }
  .menu__item {
    margin-right: 30px;
    font-size: 20px;
    color: #ffffff;
  }
  .menu__item:last-child {
    margin-right: 0;
  }
}
@media screen and (max-width: 940px) {
  .burger {
    z-index: 5;
    display: block;
    width: 30px;
    height: 18px;
    position: relative;
    cursor: pointer;
  }
  .burger::before, .burger::after, .burger__line {
    position: absolute;
    left: 0;
    border-top: 1px solid #ffffff;
    width: 100%;
    transition: all 0.4s ease 0s;
    background-color: #ffffff;
  }
  .burger::before, .burger::after {
    content: "";
  }
  .burger::before {
    top: 0;
  }
  .burger::after {
    bottom: 0;
  }
  .burger__line {
    top: 50%;
    transform: scale(1) translate(0, -50%);
  }
  .burger.active::before {
    top: 50%;
    transform: rotate(-45deg) translate(0, -50%);
  }
  .burger.active::after {
    bottom: 50%;
    transform: rotate(45deg) translate(0, 50%);
  }
  .burger.active .burger__line {
    transform: scale(0) translate(0, -50%);
  }
  .nav {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
    padding: 135px 30px 30px 30px;
    transition: left 0.4s ease 0s;
    overflow: auto;
  }
  .nav.active {
    left: 0;
  }
  .menu__item {
    flex-wrap: wrap;
    margin-bottom: 30px;
    font-size: 24px;
  }
  .menu__item:last-child {
    margin-bottom: 0;
  }
  .menu__item:hover::after {
    width: 0;
  }
}
